.tab-group {
  width: 100%;
  position: absolute;
  top: 0;
  transform: translateY(-100%);
  left: 0;
  @include flexCenter(row);
  &__tab {
    cursor: pointer;
    height: 5rem;
    width: 15rem;
    background-color: $color3;
    @include flexCenter(column);
    color: white;
    &:first-child{
      border-top-left-radius: 4px;

    }
    &:last-child{
      border-top-right-radius: 4px;
    }
    &--active {
      color: $color3;
      background-color: white;
        border: 1px solid $color3;
        border-bottom: none;
    }
  }
}

.productscreen {
  overflow: hidden;
  width: 100vw;
  margin-top: 3rem;
  display: grid;
  grid-template-columns: 1fr repeat(8, 1fr) 1fr;
    grid-auto-rows: minmax(100px, auto);
    justify-content: center;
    gap: 2rem;
  grid-template-areas: 
  '. path-group . . . . . . .  .'
  '. content content content content content rec rec rec . '
  '. var var var var var var var var  .'
  
  ;

  &__content {
    grid-area: content;
    align-self: center;
    border: 1px solid $color3;
    position: relative;
    min-height: 25rem;
  }

  &__details {
    // max-width: max-content;
    // min-width: 300px;
    justify-content: center;
  }

  &__table {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  &__recommended {
    grid-area: rec;
    border: 1px solid black;
  }

  &__variants {
    grid-area: var;

  }
}

.reviewcards {
  display: grid;
  grid-template-columns: repeat(auto-fit, 10rem);
  text-overflow: ellipsis;
  grid-auto-rows: 10rem;
  gap: 5px;
  padding-top: 1rem;
  justify-items:left;
}

.reviewcard {
  @include flexCenter(column);
  width: 10rem;
  &__comment {

  }

  &__name {

  }

  &__date {
    font-size: 1rem;
  }
}

.showreviews, .createreview {
  margin: 0 auto;
  width: 80%;
}



